
.calendarContainer {
    color: rgb(32, 32, 35);
    width: 640px;
    padding: 32px;
    background: rgb(255, 255, 255);
    border-radius: 5px;
    box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px, rgba(0, 0, 0, 0) 0px 0px 0px 1px;
    text-align: center;
}

.calendarHeader {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.calendarHeader h1 {
    padding-left: 24px;
    padding-right: 24px;
}

.toggleArrows {
    padding: 24px;
}

.arrowBack, .arrowForward {
    background-color: rgb(255, 255, 255);
    padding: 16px;
    border-radius: 100%;
    border: none;
}

.daysDisplay {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}

.weekDayDiv {
    display: flex;
    flex-direction: column;
}

.priorMonthValue, .futureMonthValue {
    color: rgb(142, 142, 142);
    margin: 16px;
}

.currentMonthValue {
    background-color: transparent;
    border-width: 0;
    font-family: inherit;
    font-size: inherit;
    font-style: inherit;
    font-weight: inherit;
    line-height: inherit;
    padding: 0;  
}

.currentMonthValue h5 {
    margin: 16px;
}

.currentMonthValue:hover, .arrowBack:hover, .arrowForward:hover {
    cursor: pointer;
}

